.container {
    max-width: 1328px;
    margin: 0 auto;
}

body {
    font-family: 'Urbanist', sans-serif;
}

a {
    text-decoration: none;
    color: #000;
}

.d-block {
    display: block;
}

.w-100 {
    width: 100%;
}

.text-center {
    text-align: center;
}

.btn {
    padding: 20px 40px;
    display: inline-block;
    font-size: 20px;
}

.btn.btn-outer {
    font-weight: bold;
    border: 1px solid;
}

.btn.btn-full{
    font-weight: bold;
    background-color: $main-color;
}

@mixin flexBox($justify-content:space-between, $align-items:center, $wrap:wrap) {
    display: flex;
    justify-content: $justify-content;
    align-items: $align-items;
    flex-wrap: $wrap;
}

section.hero-box {
    padding: 85px 0px;
    .top-title {
        font-size: 20px;
        font-weight: bold;
    }
    h2.title {
        font-size: 42px;
        font-family: 'Fredericka the Great', cursive;
    }
    p.subtitle {
        font-size: 16px;
        padding: 25px 0px 50px 0px;
    }
}

.form-group{
    .form-control{
        padding: 10px;
        width: 100%;
    }
}