#wrapper {
    header {
        min-height: 828px;
        background-image: url("./../images/systems/background-header.png");
        background-size: cover;
        background-repeat: no-repeat;
        position: relative;

        .top-header {
            @include flexBox();
            padding: 40px 0px 80px 0px;

            #main-menu {
                @include flexBox();

                .menu-item {
                    .menu-link {
                        margin-right: 60px;
                        font-size: 17px;
                        font-weight: bold;
                    }

                    &:last-child {
                        .menu-link {
                            margin-right: 0px;
                        }
                    }
                }
            }

            .box-right {
                .box-contact {
                    margin-bottom: 30px;
                    @include flexBox(right);

                    .box {
                        padding-right: 15px;
                    }

                    .title {
                        font-weight: 500;
                    }

                    .phone {
                        font-weight: 700;
                        font-size: 30px;
                    }
                }
            }
        }

        .box-banner {
            @include flexBox();
        }

        .box-banner {
            .container {
                @include flexBox();
                padding: 0px 90px 0px 45px;

                .box-right {
                    flex: 0 1 42%;
                }

                .box-left {
                    .title-question {
                        font-size: 58px;
                        font-family: 'Liu Jian Mao Cao', cursive;
                        font-weight: bold;
                    }

                    .subtitle {
                        font-size: 90px;
                        font-family: 'Fredericka the Great', cursive;
                        padding: 25px 0px 35px 0px;
                    }

                    .box {
                        display: inline-block;

                        .body-title {
                            font-size: 27px;
                            font-weight: bold;
                        }

                        .line {
                            height: 2px;
                            width: 100%;
                            background-color: #ccb301;
                            margin: 40px 0px;
                        }
                    }

                    .box-discount {
                        @include flexBox(center);

                        span {
                            font-family: 'Fredericka the Great', cursive;

                            &:first-child {
                                font-size: 20px;
                                padding-right: 10px;
                            }

                            &.math {
                                font-size: 57px;
                            }
                        }
                    }

                    .box-btn {
                        padding: 35px 0px;
                    }
                }
            }
        }

        .shape {
            position: absolute;
        }

        .shape:nth-of-type(1) {}

        .shape:nth-of-type(2) {
            top: 130px;
            left: 80px;
        }

        .shape:nth-of-type(3) {
            width: 305px;
            height: 298px;
            bottom: -150px;
            left: -150px;
        }

        .shape:nth-of-type(4) {
            left: 50%;
            bottom: 48px;
        }

        .shape:nth-of-type(5) {
            left: 50%;
            top: 180px;
        }

        .shape:nth-of-type(6) {
            right: 85px;
            bottom: 65px;
        }
    }

    .box-list-food {
        .list-food {
            @include flexBox(space-between, flex-end);

            .food-item {
                flex: 0 1 378px;

                .food-thumbnail {
                    -webkit-box-shadow: -1px 13px 42px -6px #5E5E5E;
                    box-shadow: -1px 13px 42px -6px #5E5E5E;
                    border-radius: 50%;

                    .thumbnail {}
                }

                .food-title {
                    .food-link {
                        padding: 50px 0px 25px 0px;
                        display: block;
                        font-size: 21px;
                        font-weight: bold;
                    }
                }

                .btn-order {
                    margin-top: 35px;
                }
            }
        }
    }

    .box-list-slider {
        overflow: hidden;

        .list-slider {
            @include flexBox(space-between, center);

            .slider-item {
                max-width: 31%;

                &:not(.active) {
                    position: relative;

                    &::before {
                        content: "";
                        position: absolute;
                        width: 100%;
                        height: 100%;
                        background-color: #00000073;
                    }
                }
            }
        }

        .nav-paginate {
            @include flexBox(center);
            padding: 80px 0px;

            .btn-nav {
                width: 20px;
                height: 20px;
                background-color: $color-disable;
                display: block;
                border-radius: 50%;
                margin-right: 5px;

                &.active {
                    background-color: $main-color;
                }

                &:last-child {
                    margin-right: 0px;
                }
            }
        }
    }

    .box-test {
        .list-test {
            display: grid;
            grid-template-columns: auto auto;
            grid-template-rows: 190px 190px;
            padding: 0px 50px;
            grid-gap: 30px 40px;

            .test-item {
                color: #fff;
                background-size: cover;
                background-repeat: no-repeat;

                &:nth-child(2) {
                    background-image: url("./../images/systems/test-2.png");
                }

                &:nth-child(3) {
                    background-image: url("./../images/systems/test-3.png");
                }

                &:first-child {
                    background-image: url("./../images/systems/test-1.png");
                    grid-row-start: 1;
                    grid-row-end: 3;
                }

                padding-top: 40px;
                padding-left: 50px;

                .title {
                    font-size: 25px;
                    font-weight: bold;
                }

                .description {
                    font-size: 42px;
                    font-weight: bold;
                }
            }

        }
    }

    .box-booking {
        max-width: 785px;
        margin: 0 auto;
        .form-booking {
            padding-top: 88px;
            @include flexBox(center);

            .form-group {
                flex: 0 1 50%;

                &:nth-child(even) {
                    padding-left: 25px;
                }
                margin-bottom: 28px;
                .btn{
                    padding: 9px 20px;
                    border: none;
                    width: 100%;
                }
            }
        }
    }

    footer{
        background-image: url("../images/systems/background-footer.png");
        background-repeat: no-repeat;
        background-size: cover;
        min-height: 580px;
        color: #fff;
        .box{
            @include flexBox();
            .box-left{
                flex: 0 1 50%;
                .footer-logo{
                    padding-top: 65px;
                    display: block;
                }
                .description{
                    padding: 70px 0px 165px 0px;
                    line-height: 1.4;
                }
            }
            .box-contact{
                a{
                    color: #fff;
                    display: flex;
                    justify-content: flex-start;
                    &:last-child{
                        padding-top: 15px;
                    }
                    span.fa{
                        color:$main-color;
                        font-size: 20px;
                        padding-right: 10px;
                    }
                }
            }
        }
        .copy-right{
            @include flexBox();
            .list-icon-contact{
                @include flexBox();
                .contact-link{
                    background-color: #fff;
                    width: 25px;
                    height: 25px;
                    display: block;
                    margin-right: 10px;
                    @include flexBox(center);
                    border-radius: 50%;
                }
            }
        }
    }
}